import React from 'react';
import './ForgotPassword.scss';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';
import notRobotImg from '../assets/not-robot-img.png';

class ForgotPasswordContainer extends React.Component{
  constructor(props){
    super(props);


  }

  goToIndex = () => {
      this.props.router.push('/');
  }

  goToLogin = () => {
    this.props.router.push('/login/true');
  }

  render() {
      return (
          <div className="forgot-form">
              <div className="header-section">
                  <div className="header-items">
                      <div className="header-left">
                          <img src={logoImg} className="droplet-logo" onClick={this.goToIndex}/>
                      </div>
                      <div className="header-right">
                          <label className="header-text pointer" onClick={this.goToLogin}>Login</label>
                      </div>
                  </div>
              </div>

              <div className="container history-container">
                  <div className="row">
                      <div className="offset-1 col-10">
                          <div className="step-section">
                              <h4 className="step-text">Reset Password</h4>
                          </div>
                          <div className="password-section">
                              <div className="form-group">
                                  <label htmlFor="pass">New Password</label>
                                  <input type="password" className="form-control" id="pass"
                                         placeholder="*********"/>
                              </div>
                              <div className="form-group">
                                  <label htmlFor="pass">Retype New Password</label>
                                  <input type="password" className="form-control" id="pass1"
                                         placeholder="*********"/>
                              </div>

                              <button className="btn btn-green btn-checkout" onClick={this.goToLogin}>RESET AND LOGIN</button>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      );
  }

}

export default ForgotPasswordContainer;



